import React from 'react'; import styled from 'styled-components'; 

const Content = (props) => {
        return (
                <ContentContainer>
                    {props.children}
                </ContentContainer>
            )
    }

const ContentContainer = styled.div `
    position: relative; display: flex; padding: 4px 10px 10px 10px;
    background: none; min-height: 95%; width: 100%; max-width: 100%; z-index: 2;
    `
export default Content